import React from 'react';
import Home from './components/Home'
import About from './components/About'
import USA from './components/USA'
import EU from './components/EU'
import Footer from './Footer'
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Navigation from './Navigation';

function App() {
    return (
        <Router>
            <div>
                <h2>Welcome to My Photo Gallery</h2>
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <ul className="navbar-nav mr-auto">
                    <li><Link to={'/Test'} className="nav-link"> Home </Link></li>
                    <li><Link to={'/about'} className="nav-link">About</Link></li>
                    <li><Link to={'/eu'} className="nav-link">Europe</Link></li>
                    <li><Link to={'/usa'} className="nav-link">America</Link></li>
                </ul>
                </nav>
                <hr />
                <Switch>
                    <Route exact path='/Test' component={Home} />
                    <Route path='/about' component={About} />
                    <Route path='/eu' component={EU} />
                    <Route path='/usa' component={USA} />
                </Switch>
            </div>

            <Footer />
        </Router>
    )};


export default App;


